/* Основные стили приложения */
.App {
    text-align: center;
    font-family: 'Arial', sans-serif;
    background-color: #f4f4f4;
    min-height: 100vh;
    padding: 20px;
    box-sizing: border-box;
  }
  
  .App-header {
    background-color: #282c34;
    padding: 20px;
    color: white;
  }
  
  .App-header h1 {
    font-size: 2em;
    margin: 0;
  }
  
  nav {
    margin-top: 20px;
  }
  
  nav button {
    background-color: #61dafb;
    border: none;
    color: white;
    padding: 10px 20px;
    margin: 0 10px;
    font-size: 16px;
    cursor: pointer;
    border-radius: 5px;
    transition: background-color 0.3s ease;
  }
  
  nav button:hover {
    background-color: #21a1f1;
  }
  
  /* Стили для активного контента */
  main {
    margin-top: 30px;
  }
  
  main h2 {
    font-size: 1.5em;
    margin-bottom: 20px;
  }
  
  ul {
    list-style-type: none;
    padding: 0;
  }
  
  ul li {
    background-color: #fff;
    margin: 10px 0;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
    text-align: left;
    transition: background-color 0.3s ease;
  }
  
  ul li:hover {
    background-color: #f9f9f9;
  }
  
  /* Стили для ссылок на курсы */
  a {
    color: #61dafb;
    text-decoration: none;
  }
  
  a:hover {
    text-decoration: underline;
  }
  
  /* Стили для профиля */
  button {
    background-color: #61dafb;
    border: none;
    color: white;
    padding: 10px 20px;
    margin: 10px 5px;
    cursor: pointer;
    border-radius: 5px;
    transition: background-color 0.3s ease;
  }
  
  button:hover {
    background-color: #21a1f1;
  }
  